// inputs

$animation-duration: 300ms;
label, input, select, textarea {
  display: block;
  margin-bottom: $margin;
  text-align: left;
  border: none;
  outline: none;
}

textarea {
  width: 100% !important;
}

input[type=radio], input[type=checkbox] {
  display: none;
}

ul {
  &.pages, &.questions {
    padding: 0;
    margin: 0;
    list-style: none;
  }
}

input[type=radio] + label,
input[type=checkbox] + label,
input[type=checkbox] + div {
  cursor: pointer;
  display: block;
  margin: 0;
  padding: $padding * 2;
  padding-right: $padding * 5;
  text-align: left;
}

input[type=radio]:checked + label,
input[type=checkbox]:checked + label,
input[type=checkbox]:checked + div {
  background: $blue;
  color: $white;
}

input[type="date"],
input[type="email"],
input[type="search"],
input[type="tel"],
input[type="time"] {
  background-color: $white;
}
input[type="date"] {  // iOS "workaround".
  height: 39px;
}

textarea, input[type="text"], input[type="password"], input[type="date"], input[type="time"], textarea, input[type="tel"], input[type="email"], input[type="search"], select {
  @include vendor(border-radius, 5px);
  @include vendor(transition, all $animation-duration ease);
  overflow: hidden;
  -webkit-appearance: none;
  width: 100%;
  padding: $padding;
  font-family: 'RobotoRegular';
  font-size: 14px;
}

textarea:focus, input:focus {
  box-shadow: 0 0 0px 2px $blue;
}

input.ng-dirty.ng-invalid,
select.ng-dirty.ng-invalid,
form.submitted-invalid select.ng-invalid,
form.submitted-invalid input.ng-invalid {
  box-shadow: 0 0 0px 2px $red;
}
.field-status {
  text-align: left;
  .valid {
    color: $green;
  }
  .invalid {
    color: $red;
  }
}

textarea, .custom:not(.icon-rack) {
  @include vendor(border-radius, 5px);
  position: relative;
  width: 95%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: $margin;
}

.custom.check, .custom.radio {
  width: 100%;
}

//custom radio

.custom.radio {

  .circle-wrap {
    float: right;
    display: block;
    background: $white !important;

    @include vendor(border-radius, 50%);
    position: absolute;
    top: 50%;
    right: $padding * 3;
    margin-top: -10px;
    width: 20px;
    height: 20px;

    .circle {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;

      @include vendor(border-radius, 50%);
      @include vendor(transform, scale(0.0));
      @include vendor(transition, all $animation-duration ease)
    }
  }
}

input[type=radio]:checked + label {

  .circle {
    $scale-transform: scale(0.6);
    background: $blue;
    -webkit-transform: $scale-transform !important;
    -moz-transform: $scale-transform !important;
    -ms-transform: $scale-transform !important;
    -o-transform: $scale-transform !important;
    transform: $scale-transform !important;
  }

  .arrow-left {
    @include triangle(left, $blue, 10px);
  }

  &.i-0 {
    background: $green_dark;
    border: none;
    .circle {
      background: $green_dark;
    }
  }
  &.i-1 {
    background: $red_dark;
    border: none;
    .circle {
      background: $red_dark;
    }
  }
}

//custom check
.custom.check {

  .square-wrap {
    float: right;
    display: block;
    background: $white;
    width: 20px;
    height: 20px;
    position: absolute;
    top: 50%;
    right: $padding * 3;
    margin-top: -10px;
    overflow: hidden;

    .fa-check {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      z-index: z("icon");
      color: $blue;
      font-size: 20px;
    }

    .white-overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background: $white;
      z-index: z("icon-overlay");

      @include vendor(transition, all $animation-duration ease);
    }
  }

}

.custom.check.white {
  background: $bg_grey;
}

input[type=checkbox]:checked + label,
input[type=checkbox]:checked + div {

  .white-overlay {
    @include vendor(transform, translateX(20px));
  }
}

.registration-form {
  .custom.check {
    padding-left: 30px;

    .square-wrap {
      left: 0;
      background: $bg_grey;

      .white-overlay {
        background: $bg_grey;
      }
    }
  }
  input[type=checkbox]:checked + label,
  input[type=checkbox]:checked + div {
    background: none;
    color: $black;
  }

}
// custom selectboxes

.custom.select {
  $select-height: 39px;
  cursor: pointer;

  background: $white;

  .arrow-wrapper {
    background: $blue;
    position: absolute;
    right: 0;
    width: 35px;
    height: $select-height;
    @include vendor(border-radius, 0 5px 5px 0);

    .arrow-up, .arrow-down {
      position: absolute;
      left: 9px;
    }

    .arrow-up {
      top: 11px;
      @include triangle(up, $white, 8px);
    }

    .arrow-down {
      bottom: 1px;
      @include triangle(down, $white, 8px);
    }
  }

  select {
    cursor: pointer;
    position: relative;
    z-index: z("icon-select");
    font-size: 14px;
    color: $black;
    background: none;
    height: $select-height;
  }
}

// scale

.scale-wrapper {
  position: relative;

  .arrow-wrapper {
    float: left;
    width: 5px;
    display: block;
    background: $grey;
    height: 100%;
    position: absolute;
    margin-left: 2%;

    @media (max-width: $breakpoint-small-max) {
      margin-left: 3%;
    }

    .arrow-up, .arrow-down {
      position: absolute;
      display: block;
      left: -4px;
    }

    .arrow-up {
      top: -7px;
      @include triangle(up, $grey, 7px);
    }
    .arrow-down {
      bottom: -14px;
      @include triangle(down, $grey, 7px);
    }
  }

  .custom {
    width: 92%;
    position: relative;
    right: 0;
    margin-left: 8%;
    background: $white;
    @include vendor(border-radius, 0);

    @media (max-width: $breakpoint-small-max) {
      width: 89%;
      margin-left: 11%;
    }

    .circle-wrap {
      float: left;
      right: auto;
      left: -8.1%;

      @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
        left: -7.9%;
      }

      @media (max-width: $breakpoint-small-min) {
        left: -10.4%;
      }
    }

    .arrow-left {
      position: absolute;
      @include triangle(left, $white, 10px);
      top: 50%;
      margin-top: -10px;
      left: -10px;
    }
  }
}

.custom.yes-no {
  float: left;
  width: 50%;

  label {
    text-align: center;
    height: 250px;
    line-height: 250px;
    width: 98%;
    border-bottom: inset 6px rgba(0, 0, 0, .2);
    border-radius: 5px;
    margin: 0;
    padding: 0;

    &.i-0 {
      background: $green;
    }
    &.i-1 {
      background: $red;
      margin-left: 5px;
    }
  }

}

// icon rack

.custom.icon-rack {

  padding: $padding/2;
  label {
    padding: $padding/2;
  }
}

.grid-header {

  h3 {
    padding: $padding;
    font-size: 12px;
    text-transform: uppercase;
    background: $grey;
    color: $black;
  }
}

.image-upload {
  .image-display {
    text-align: center;

    .upload-image {
      width: auto;
      max-height: 300px;
    }
    .uploaded-image {
      width: 50%;
    }
  }
}
