
.question{
	.datepicker.dropdown-menu {
		margin-top:-100px;
	}
}

.diary .question {
	.datepicker.dropdown-menu {
		margin-top:-150px !important;
	}
}

.datepicker.dropdown-menu {
		position:absolute;
		width: 300px;
		background:white;

		z-index:100;

		padding:$padding;


		//btn btn-default pull-left
		//btn btn-default pull-right
	button {
		outline: none;
		border: 0px;
		padding:$padding;
		@include vendor(border-radius, 5px);
	}

	tbody {
		height: 180px;
	}

	.btn{
		padding: $padding !important;
	}
	.btn:active{
		background:$blue;
		color:white;
	}
	.pull-left, .pull-right{
		background:none;
	}
	
	tbody button {
		padding: 6px;
		background:none;
	}

	tbody button.btn-primary{
		background:$blue;
		color:white;
	}
	&.datepicker-mode-1, &.datepicker-mode-2 {
		tbody button {
			height: 65px;
		}
	}
}

.timepicker.dropdown-menu {
	padding: 0 4px;
	button {
		outline: none;
		border: 0px;
	}
	tbody button {
		padding: 6px;
	}
}