.licenses {

  li {
    cursor: pointer;
  }
  p {
    color: $black;
  }

  a {
    color: $black;
    text-decoration: none;
    position: relative;
    z-index: z("licenses-button");
  }

  .fat a {
    display: block;
  }

  .license_content {
    img {
      width: 30%;
    }
    @include vendor(border-radius, 5px);
    margin: 15px 5px 5px;
    padding: 10px;
    background-color: rgba(0, 0, 0, .05);
  }

  .link {
    color: $blue_dark;
    text-decoration: underline;
    padding: 5px 0;
  }

  .license-arrow-up, .license-arrow-down {
    display: block;
    float: right;
    position: absolute;
    top: 15px;
    right: 15px;
  }

  .license-arrow-up {
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid $grey_dark;
  }

  .license-arrow-down {
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;

    border-top: 10px solid $grey_dark;
  }

  .copy {
    line-height: 1.4;
    font-size: 12px;
    color: $black;
    margin: 10px 0;
  }

}