.reward {
  text-align: center;
  background-color: white;
  padding-bottom: 100px;

  .reward-star {
    background: url("../img/ui/reward-star-icon.png") center center no-repeat;
    background-size: 130px;
    height: 130px;
    position: relative;

    .points {
      position: absolute;
      top: 50%;
      left: 0;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      font-size: 23px;
      font-weight: bold;
      color: white;
      text-align: center;
      width: 100%;
    }
  }

  .button.disabled {
    color: $grey_dark !important;
  }

  h2 {
    font-size: 24px;
    text-align: center;
    color: $black;
    margin: 10px 0;
  }
  .desc {
    color: $grey;
    text-align: center;
  }

}