// badge
.badge {
  width: 30px;
  height: 30px;
  position: relative;
  top: -2px;
  left: 5px;
  background-color: $blue;
  box-shadow: 0 8px 20px rgba(0, 0, 0, .5);

  img {
    width: 80%;
    margin: 4px;
  }
  .arrow-down {
    @include triangle(down, black, 15px);
    border-top-color: $blue;
    position: absolute;
    bottom: -30px;
  }
}

// white arrow container
.white-img-container {
  position: relative;
  background: white;
  padding: $padding;
  margin-bottom: $margin * 2;

  img {
    height: 140px;
    width: auto;
    display: block;
    margin: 0 auto
  }

  .arrow {
    display: block;
    @include triangle(down, $white, 20px);
    position: absolute;
    left: 50%;
    margin-left: -20px;
  }

  p {
    width: 100% !important;
    margin-bottom: 15px !important;
    text-align: left;
  }
}

///
///  page description
///

.happy-guy {

  .guy {
    width: 200px;
    background-image: url("../img/ui/happy_guy-2.jpg");
    height: 250px;
    background-position: bottom center;

    background-size: 80%;
    background-repeat: no-repeat;
  }

  .bubble {

    background: $white;
    padding: 10px;
    z-index: z("happy-guy-bubble");
    position: relative;

    .arrow-down {
      @include triangle(down, $white, 10px);
      position: absolute;
      bottom: -20px;
      right: 50%;
    }
  }
}

// next prev buttons
.nav {
  padding: $padding;
  .secondary {
    float: left;
    position: relative;
  }
  .submit, .close, .standard, .cta {
    float: right;
  }
}

// empty
.empty {

  .image {
    background-image: url("../img/ui/empty-activities-icon.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 200px;
    width: 200px;
    height: 200px;
    display: block;
    margin: 0 auto;
  }
  p {
    text-align: center;
  }
}

//scroll
.scroll {
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  //-webkit-backface-visibility:hidden;
  //-webkit-transform:translate3d(0,0,0);
}

//overflow
.overflow-hidden {
  overflow: hidden !important;
}

// mobile activity join button
.mobile-button {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
//  z-index: z("sticky-top-bar") - 1;
// TODO: Test and ensure that the opener elements are click-/tappable.
  display: block;
  height: 100%;
  opacity: 0;

  a {
    width: 100%;
    height: 100%;
    position: absolute;
  }
}

$brand-bar-height: $base-h1-line-height + (2 * $brand-bar-y-padding);
.view {
  position: absolute;
  left: 0;
  bottom: 0;
  &#main-wrapper {
    top: 0;
  }
  &.main-content-wrap {
    @media (max-width: $breakpoint-small-max) {
      top: 0;
    }
    top: $brand-bar-height;
  }
}

