// branded
.branded {
  @include vendor(transition, all 400ms ease);
}

.branded-header {
  background-position: 10px center;

  @media (max-width: $breakpoint-small-max) {
    background-position: center;
  }
  background-repeat: no-repeat;
  background-size: auto 80%;

  h1 {
    visibility: hidden;
  }
}

// wrapped list

//wrapped list

.wrapped-list:first-of-type {
  margin-top: 10px;
}

.wrapped-list {
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  background: $white;
  border-radius: 5px;
  margin-bottom: 20px;
  -webkit-box-shadow: 0 2px 0 #cfcfcf;
  list-style: none;

  li {
    margin: 0;
//    padding: 0;
    list-style: none;
    border-radius: 5px;
    position: relative;
    padding: 10px;
    border-bottom: 1px solid #d0d0d0;

    &.e-link {
      text-align: center;
      font-size: 16px;
      padding: 0;
      a {
        padding: 10px;
        display: block;
      }
    }
    .center {
      text-align: center;
      float: none !important;
    }

    p {
      margin: 0;
      font-size: 16px;
    }

    .small {
      font-size: 10px;
      margin-bottom: 5px;
    }

    .fat {
      color: $black;
      font-family: RobotoRegular;
    }
    .light {
      color: $grey_dark;
      font-family: RobotoLight;
    }
    .f-left {
      float: left;
    }
    .f-right {
      float: right;
    }

    .c-arrow-right {
      width: 0px;
      height: 0px;
      border-style: solid;
      border-width: 6px 0 6px 7px;
      border-color: transparent transparent transparent $blue;
      position: relative;
      top: 3px;
      margin-left: 10px;
    }

  }

  li.active {
    background: $grey !important;

    p {
      color: white !important;
    }
  }

  li:last-of-type {
    border: none;
    border-radius: none !important;
  }

}

.burger.fa-bars {
  color: $white;
  position: absolute;
  top: 0;
  left: 0;
  padding: 8px;
}

.icon-button {
  color: $white;
  &:hover, &:active{
    text-decoration: none;
    color: $white;
    opacity: 0.8;
  }
}

// burger
// hamburger
.burger {
  position: absolute;
  top: 0;
  left: 0;
  width: 55px;
  padding: 12px;

  .line {
    width: 100%;
    display: block;
    margin-bottom: 4px;
    height: 4px;
    background: white;
  }

  &:active {
    background: white;

    .line {
//      background: $blue;
    }
  }
}

.cfp-hotkeys-close {
  padding: 10px 10px;
}
