@font-face {
	font-family: 'encount-icons';
	src: url('fonts/encount-icons.eot');
}
@font-face {
	font-family: 'encount-icons';
	src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'),
		 url(data:application/font-woff;charset=utf-8;base64,) format('woff');
	font-weight: normal;
	font-style: normal;
}

.ei {
	font-family: 'encount-icons';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.ei-scale-smiley-1:before {
	content: "\25";
}

.ei-scale-smiley-2:before {
	content: "\24";
}

.ei-scale-smiley-3:before {
	content: "\23";
}

.ei-scale-smiley-4:before {
	content: "\22";
}

.ei-scale-smiley-5:before {
	content: "\21";
}

.ei-coin-thin:before {
	content: "\3f";
}

.ei-coin:before {
	content: "\43";
}

.ei-coin-filled:before {
	content: "\43";
}

.ei-fa-list:before {
	content: "\f03a";
}

.ei-fa-list-ul:before {
	content: "\f0ca";
}

