$home-slider-height: 400px;
$home-slider-height-small: $home-slider-height / 2;
$desc-box-offset: 30px;

.slider {
  position: relative;

  ul[rn-carousel] {
    margin: 0;

    height: $home-slider-height;
    @media (max-width: $breakpoint-small-max) {
      height: $home-slider-height-small;
    }
  }

  .slide {
    background-size: cover;
    background-repeat: no-repeat;

    height: $home-slider-height;
    @media (max-width: $breakpoint-small-max) {
      height: $home-slider-height-small;
    }

    .desc-box {
      display: inline-block;
      background: rgba(0, 0, 0, .5);
      position: absolute;
      top: $desc-box-offset;
      left: $desc-box-offset;
      bottom: $desc-box-offset;

      h2, p {
        color: $white;
      }

      h2 {
        @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
          font-size: 18px;
        }
      }
      p {
        @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
          font-size: 12px;
        }
      }

      .content {
        padding: $padding * 2;
        padding-top: 5px;
      }

      .sh {
        text-align: center;
        position: absolute;
        width: 100%;
        bottom: 0;
      }
    }

    .mobile-desc {
      .top {
        background: black;
        position: absolute;
        top: 0;
        width: 100%;

        .badge {
          position: absolute;
          top: 0;
          left: 10px;
        }
        .meta, .date {
          padding: 5px 0;
        }
        .meta {
          color: $yellow;
          text-transform: uppercase;
          float: left;
          padding: 0;
          position: relative;
          left: 50px;
          margin: $margin/2;
        }
        .points-date {
          float: right;
          position: absolute;
          right: 10px;
          margin: $margin/2;
        }
      }

      .bottom {
        background: rgba(0, 0, 0, .5);
        position: absolute;
        bottom: 20px;
        width: 100%;

        h2 {
          color: white;
          margin: $margin;
          font-size: 16px;
          line-height: 20px;
        }
      }
    }
  }

  .rn-carousel-indicator.phone {
    position: absolute;
    left: 0;
    bottom: 0;
  }

  .rn-carousel-indicator {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    height: 20px;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;

    span {
      -webkit-transition: color .2s ease-out;
      transition: color .2s ease-out;
      padding: 0 5px;
      color: #333;
    }

    .active {
      color: white;
    }
  }
}

.carousel-thumbs {
  position: absolute;
  background: none !important;
  right: $desc-box-offset;
  bottom: $desc-box-offset;
  width: 32%;

  .thumb {
    display: inline-block;
    border: 1px solid transparent;
    width: 33.33333%;
    transition: border 0.2s ease-in;

    &.is-active {
      box-shadow: 0 0 0 1px $white;
    }

    img {
      width: 100%;
      height: auto;
    }
  }
}
