// main

#main-container.make-left {
  margin-left: 0;
  margin-right: auto;
}

#main-container {

  @media (min-width: $container-max-width) {
    @include vendor(box-shadow, 0px -30px 56px -16px black);
  }
}

.open-left {
  @media (max-width: $breakpoint-small-max) {
    #main-wrapper {
      -webkit-transform: translate3d(65%, 0, 0);
      transform: translate3d(65%, 0, 0);
    }
  }
}

.open-right {
  @media (max-width: $breakpoint-small-max) {
    #main-wrapper {
      -webkit-transform: translate3d(-65%, 0, 0);
      transform: translate3d(-65%, 0, 0);
    }
  }
}

#main-content {
  position: relative;
  background: $bg_grey;
  margin-top: 0;

  @media (max-width: $breakpoint-small-max) {
    -webkit-transition: all 300ms ease;
    transition: all 300ms;
    top: $menu-bar-height;
    padding-bottom: $menu-bar-height;
  }
}