.growl-container {
  position: fixed;
  top: 0;
  right: $margin;
  z-index: z("growl-container");
  width: 350px;

  @media (max-width: $breakpoint-small-max) {
    width: 100%;
    right: 0;
  }

  li {
    width: 100%;
    padding: 10px;
    list-style: none;
    @include vendor(border-radius, 5px);
    background: $black;
    color: #d4dfe5;
    margin: $margin 0;
  }
  .error {
    color: #6d2021;
    background: $red_dark;
  }
}