//$animation-duration: 350ms;
$animation-duration: 350ms;
$easeOutQuad: cubic-bezier(0.250, 0.460, 0.450, 0.940);
$global-easing: $easeOutQuad;

$delay: .3s;

$progress-bar-height: 51px;

.shows-report,
.shows-questionnaire {
  .cl-modal-box .cl-contents {
    overflow: hidden;
  }

  .scrollable-area {
    position: absolute;
    top: $progress-bar-height;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow-y: auto;
    background-color: $bg_grey;
  }
}

.cl-modal-box {
  @media (min-width: $breakpoint-small-min) {
    .slide-container .pages {
      padding: $padding;
    }

    .grid-rows {
      padding: 0 $padding;
    }
  }
}

.slide-container {
  .slides {
    position: relative;
    white-space: nowrap;
    overflow: visible;
    @include vendor(transform, translate3d(0, 0, 0));
    padding: 0;
    margin: 0;

    .slide {
      white-space: normal;
      vertical-align: top;
      display: block;
      width: 100%;
      height: 100%;
      @include vendor(backface-visibility, hidden);
    }

    //&.forward,
    &.slide-in-from-right {
      &.animate-page .page-slide,
      &.animate-question .question-slide {

        &.ng-hide-add,
        &.ng-hide-remove {
          display: inline-block !important;
          @include vendor(transform, translate3d(0, 0, 0));
          transition: all $delay;
          top: 0;
        }
        &.ng-hide-add-active,
        &.ng-hide-remove-active {
          @include vendor(transform, translate3d(-100%, 0, 0));
        }
      }
    }

    &.slide-in-from-left {
      &.animate-page .page-slide,
      &.animate-question .question-slide {

        &.ng-hide-add,
        &.ng-hide-remove {
          display: inline-block !important;
          @include vendor(transform, translate3d(-100%, 0, 0));
          transition: all $delay;
          top: 0;
        }
        &.ng-hide-add-active,
        &.ng-hide-remove-active {
          @include vendor(transform, translate3d(0, 0, 0));
        }
      }
    }
  }

  .grid-rows {
    $animation-height: 300%;
    -webkit-perspective: 1000;
    -webkit-backface-visibility: hidden;
    @include vendor(transform, translate3d(0, 0, 0));

    &.slide-in-from-bottom {

      &.transition-add {
        transition: $delay/2 linear all;
        @include vendor(transform, translate3d(0, 0, 0));
      }
      &.transition-add-active {
        @include vendor(transform, translate3d(0, $animation-height, 0));
      }

      &.transition-remove {
        transition: $delay/2 linear all;
        @include vendor(transform, translate3d(0, $animation-height, 0));
      }
      &.transition-remove-active {
        @include vendor(transform, translate3d(0, 0, 0));
      }
    }
  }
}

// grids
.grow-box {
  //  margin-top:;

  .guy {
    height: 100%;
  }

  .custom.yes-no {
    height: 100%;
    padding-bottom: 20px;
    margin-bottom: 0;

    label {
      height: 100%;
      padding-bottom: 0;
    }
  }
}

.grid-container.custom {
  margin-bottom: 0;
}

.white-img-container.is-grid {
  margin-bottom: 0;
  border-bottom: 10px solid silver;
}

$grid-carousel-indicator-padding: 5px;

.grid-carousel {
  background: silver;
  margin-bottom: $margin;
  position: relative;
  height: 100%;

  ul[rn-carousel] {
    height: 50px;
  }

  .row {
    text-align: center;
    p {
      display: inline-block;
      background: $blue;
      padding: $padding $padding * 2;
      margin: $margin;
      position: relative;
      border-radius: 8px;
      @include vendor(box-shadow, inset 0 -3px 0 rgba(0, 0, 0, 0.1));
      color: $white;
    }
  }

  .rn-carousel-indicator {
    text-align: center;
    position: absolute;
    left: 0;
    width: 100%;
    padding: 0 5px;
    bottom: 0;
    background: none;

    span {
    &::before {
      content: "\f096";  // fa-square-o
      padding: 0 $grid-carousel-indicator-padding;
    }
    &.green {
      &::before {
        content: "\f046";  // fa-check-square-o
        padding: 0 ($grid-carousel-indicator-padding - 2px) 0 $grid-carousel-indicator-padding;
      }
    }

    &.green.active {
//      color: $green_dark;
    }

    }

  }

}

// yes no grid
.yes-no-grid {
  height: 100%;

  & > .header {
    background: silver;
    color: white;
    text-align: center;
    font-size: 18px;
    padding-bottom: 10px;
    margin: 0;
  }
  .padding {
    $padding: 2.5%;
    display: inline-block;
    width: 100%;
    height: 100%;
    padding: $padding $padding 20px $padding;
  }
}

// progress bar
.progress {
  height: $progress-bar-height;
  overflow: hidden;
  background: white;
  padding: 10px 15px;
  box-shadow: inset 0 -1px 0 $bg_grey;

  p {
    text-transform: uppercase;
    color: $grey_dark;
    display: inline-block;
    font-size: 10px;
    margin: 0 0 5px 0 !important;
    width: 100% !important;
    text-align: left;
  }
  .percentage {
    float: right;
    font-size: 10px;
  }
  .progress-bar {
    position: relative;
    display: block;
    background: $bg_grey;
    height: 5px;
    overflow: hidden;

    .bar {
      -webkit-transition: width 200ms ease;
      height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      background: $green;
    }

  }
}

.foursquare-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 95%;

  li {
    background: #fff;
    margin-top: 1px;
    padding: 10px;
  }
}

.smiley {
  .scale-container {
    height: 100%;
  }
  .scale {
    @media (max-height: 500px) {
      height: 260px !important;
    }
    height: 300px !important;
    overflow: hidden !important;
    background: none !important;
    padding-top: 45px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;

    .face-scale {
      $y-pull-down: 80px;
      -webkit-transform: translate3d(0, $y-pull-down, 0);
      -ms-transform: translate(0, $y-pull-down);
      transform: translate(0, $y-pull-down);
      -webkit-transition: all $animation-duration ease;
      transition: all $animation-duration ease;
      display: block;
      position: relative;
      z-index: z("icon");
      height: 100%;
      float: right;
      width: percentage(1/5);

      .face {
        span {
          font-size: 63px;
          display: block;
          text-align: center;
          opacity: 0.7;
        }
      }
    }

    .face-scale.f-1 {
      background: $red;
    }
    .face-scale.f-1.selected {
      background: $red_dark;
    }

    .face-scale.f-2 {
      background: $yellow;
    }
    .face-scale.f-2.selected {
      background: $yellow_dark;
    }

    .face-scale.f-3 {
      background: $purple;
    }
    .face-scale.f-3.selected {
      background: $purple_dark;
    }

    .face-scale.f-4 {
      background: $blue;
    }
    .face-scale.f-4.selected {
      background: $blue_dark;
    }

    .face-scale.f-5 {
      background: $green;
    }
    .face-scale.f-5.selected {
      background: $green_dark;
    }

    .face-scale:hover,
    .face-scale.touch-hover,
    .face-scale.selected {
      -webkit-transform: translate3d(0, 0, 0);
      -ms-transform: translate(0, 0);
      transform: translate(0, 0);
      -webkit-transition: all 100ms ease-out;
      transition: all 100ms ease-out;
    }
  }
}

.navigator {
  text-align: center;
  .close {
    float: none;
  }
}
