.profile {
  display: block;
  position: relative;

  .top {
    background: $default-brand-color;
    position: relative;
    box-shadow: inset 0 -2px 0 $dark-overlay;

    .image {
      padding: $padding;
      .circle {
        @include vendor(border-radius, 50%);
      }
    }

    h2 {
      display: block;
      color: white;
      width: 100%;
      padding: $padding;
    }

    .meta {
      margin: 0;
      position: relative;

      .item {
        padding: $padding;

        * {
          color: $white;
        }

        p {
          margin: 0;

        }

        .big {
          color: white;
          font-size: 18px;

          .i {
            color: $white;
            margin: 0 3px;
            font-size: 18px;
            position: relative;
            top: 1px;
          }
        }
        .small {
          text-transform: uppercase;
          font-size: 10px;
          color: $white;
        }
      }
    }

    .edit-profile-button {
      background: $dark-overlay;
      margin: $margin;
    }

  }

  .edit-form {
    margin-top: $margin * 2;
    margin-left: auto;
    margin-right: auto;
    width: 95%;

    label {
      text-transform: uppercase;
      font-family: 'RobotoLight';
    }

    .country,
    .gender,
    .language,
    .phone-number {
      width: 100%;
    }
  }

  .edit-thumb {

    @media (max-width: $breakpoint-small-max) {
      width: 100px;
    }

    @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
      width: 200px;
    }

    @media (min-width: $breakpoint-large-min) {
      width: 200px;
    }

    margin: 10px auto;
    display: block;
  }
}

#jcrop {
  background: $bg_grey;
}

.img-cropper {
  img {
  }

  .edit-profile-button {
    margin: $margin;
    z-index: z("img-cropper");
    cursor: pointer;

    input[type="file"] {
      cursor: pointer;
    }
  }
}