.reports {

  .me {
    display: inline-block;
    font-size: 19px;
    position: relative;
    margin: 0 2px;
    color: $grey_dark;
  }

  .white-img-container {
    padding: 5px 0;

    h3 {
      font-family: RobotoRegular;
      text-transform: uppercase;
      font-size: 12px;
      color: $grey_dark;
      margin-bottom: 10px;
      margin-top: -6px;
    }
    p {
      color: $black;
      padding: $padding;
    }
  }

  .scroller {
    bottom: 50px;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAJElEQVQIW2NkgIJ79x/+V1KUZwRxwQQIoAiCODAJGI1dJTbtAB4YE6PaCMxZAAAAAElFTkSuQmCC) repeat;
  }

  //
  // smiley face
  //
  .scale {
    margin: 0;
    padding: 0;
  }
  .smiley .scale {
    height: 330px !important;
    background: none !important;
    position: relative;
    bottom: 0;
    width: 100%;
    overflow: visible;

    .face-scale {
      .me-indicator {
        position: absolute;
        top: -30px;
        width: 100%;
        text-align: center;

      }
    }
  }

  //
  // bar, horizontal
  //

  .bar {
    position: relative;
    width: 100%;
    display: block;
    margin-bottom: 10px;

    .me {
      margin: 0 2px;
    }

    .bar-progress {
      float: left;
      width: 20%;
      height: 40px;
      background: $blue;
      box-shadow: inset 0 -4px 0 #{'rgba(0,0,0,.2)'};
    }
    .percentage {
      float: left;
      width: 20%;
      position: relative;
      height: 40px;
      margin-bottom: 0;
      span {
        font-size: 15px;
        color: $black;
        position: absolute;
        top: 50%;
        -webkit-transform: translate(0%, -50%);
        margin-left: 10px;
      }
    }

    p {
      color: $grey_dark;
      margin-top: 2px;
    }
  }

  //
  .icon-rack {
    .img {
      float: left;
      height: 60px;
      width: 60px;
      height: auto;
    }
    .bar-progress {
      height: 60px;
    }
    .percentage {
      height: 60px;
    }

    .me-indicator {
      top: 41px;
      position: relative;
      left: -33px;
      .me {
        font-size: 16px;
      }
    }
  }

  //
  // yes no
  //

  .yes-no-container {
    height: 320px;
    position: relative;
    width: 100%;
    left: 0;
    bottom: 0;

    .yesno {
      float: left;
      width: 50%;
      position: relative;
      height: 100%;
    }

    .Yes {
      .progress {
        background: $green;
      }
    }
    .No {
      .progress {
        background: $red;
      }
    }

    .yes-no-bar {
      height: 100%;
      p {
        position: absolute;
        width: 100%;
        text-align: center;
        top: 0px;
        font-size: 18px;
        color: $black;
      }
      .progress {
        display: block;
        width: 95%;
        position: absolute;
        top: 20px;
        bottom: 50px;
        -webkit-transform: translate(-50%, 0);
        left: 50%;
        box-shadow: inset 0 -4px 0 #{'rgba(0,0,0,.2)'};
      }
    }

    .txt {
      position: absolute;
      width: 100%;
      text-align: center;
      bottom: 20px;
      left: 0;
      font-size: 18px;
      color: $grey;
    }
  }

  //
  // donut chart
  //
  .chart {
    text-align: center;
    position: relative;
    height: 400px;
  }
  .chart-wrap {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -200px;

    @media(max-width: $breakpoint-small-max) {
      zoom: 0.9;
      @include vendor(transform, scale(0.9));
    }

    .nvd3.nv-pie path {
      stroke: none;
    }
  }
  .nv-label rect {
    stroke: none !important;
    fill: none !important;
  }
  //legends
  .legend {
    position: relative;
    color: $grey_dark;
    font-size: 14px;
    margin-bottom: 10px;

    .square {
      height: 15px;
      width: 15px;
      display: inline-block;
      top: 3px;
      position: relative;
      margin-right: 5px;
      box-shadow: inset 0 -2px 0 #{'rgba(0,0,0,.15)'};
    }

    .percentage {
      color: $black;
      margin-left: 5px;
    }
  }

  //
  // stacked bar chart
  //

  .stacked {

    height: 50px;
    display: block;
    margin: 20px 0;

    .single {
      position: relative;
      float: left;
      display: block;
      height: 100%;
      box-shadow: inset 0 -4px 0 #{'rgba(0,0,0,.15)'};

      .percent {
        position: absolute;
        top: -20px;
        width: 60px;
        text-align: left;
        font-size: 12px;
      }

    }

    .zero {
      //width: 2px !important;

      .percent {
        display: none;
      }
    }
  }
}