.avoid-brand-flicker {
  visibility: hidden;
}

.jcrop-holder {
  > div {
    background-color: rgba(255, 255, 255, 0.1);

    > div {
      @include vendor(border-radius, 50% !important);

      .jcrop-vline, .jcrop-hline {
        margin: -1px; // Remove stippled line fragments.
      }
    }
  }
}

// button
button {
  border: 0;
}

.button, .fake-input {
  @include vendor(border-radius, 5px);
  padding: $padding/2 $padding*2;
  color: $white;
  display: inline-block;
  position: relative;
  overflow: hidden;
  @include vendor(box-shadow, inset 0 -3px 0 $dark-overlay);
  height: 34px;
}

.button:hover {
  text-decoration: none;
  color: $white;
  opacity: 0.8;
}

.small-btn {
  display: block;
  color: $white;
  text-decoration: underline;
  text-transform: uppercase;
  font-size: 12px;
}

.button.disabled {
  color: #c2c0c0 !important;
  background: #dbdbdb !important;
}

.button.disabled.ng-click-active {
  color: #c2c0c0 !important;
  background: #dbdbdb !important;
}

.button.ng-click-active:not(.notifications) {
  @include vendor(box-shadow, inset 0 -3px 0 #{'rgba(0,0,0,.15)'});
  overflow: hidden;
}

.button.full-width {
  width: 100% !important;
  display: block;
  margin: 10px auto 10px auto;
  text-align: center;
}

//standard

.button.standard, .edit-profile-button.standard {
  background: $blue;
}

.button.standard.ng-click-active, .edit-profile-button.standard.ng-click-active {
  background: #59a6cb;
}

//secondary
.button.secondary {
  background: $light_grey;
}

.button.secondary.ng-click-active {
  background: #afafaf;
}

//cta
.button.cta, .fake-input.cta {
  background-color: $green;
}

.button.cta.active, .fake-input.cta.active {
  background-color: $green_dark;
}

//cancel
.button.cancel {
  background: $red;
}

.button.cancel.active {
  background: $red_dark;
}

// grey dismiss
.button.grey {
  background: none;
  box-shadow: none;
  color: grey;
  text-transform: uppercase;
  font-size: 11px;
  text-decoration: underline;
}

.button.grey.active {
  background: $grey_dark;
  color: $white;
}

//facebook
.button.fb {
  background: #3b5998;

  .icon-facebook {
//    margin: 0 5px 0 -16px;  // <-- Martin's suggestion.
    margin: 0 5px 0 0;      // <-- Dag's suggestion.
  }
}

.fb.active {
  background: #162f66;

  .icon-facebook {
    background: #0f214f;
  }
}

.fake-input input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: red;
  height: 100%;
  opacity: 0;
}

.main-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  cursor: pointer;

  @media (min-width : $breakpoint-small-min){
    display: none;
  }

  z-index: z("main-overlay");
}