.activity-preview {
  background: $bg_grey;

  .image {
    height: 200px;
    position: relative;
    background-size: cover;
    background-position: center;
  }
  .top {
    background: black;
    position: absolute;
    top: 0;
    width: 100%;

    .badge {
      position: absolute;
      top: 0;
      left: 10px;
    }
    .meta, .date {
      padding: 5px 0;
    }
    .meta {
      color: $yellow;
      text-transform: uppercase;
      float: left;
      padding: 0;
      position: relative;
      left: 50px;
      margin: $margin/2;
    }
    .points-date {
      float: right;
      position: absolute;
      right: 10px;
      margin: $margin/2;
    }
  }

  .content {
    position: relative;
    padding: $padding;
  }

  &.buttons {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: $padding;
    text-align: center;
  }
}