.sticky-carousel {
  background-color: $bg_grey;
  z-index: z("sticky-carousel");
  position: fixed;
  top: 0;

  .message .core {
    margin-bottom: 0;
  }

  .attachment .image {
    max-height: none;
  }

  &.empty .fold {
    height: 0;
  }

  .fold {
    height: 15px;
    @include background-image(
            linear-gradient(rgba($bg_grey, 1), rgba($bg_grey, 0)));
  }
}



