$dur: 300ms;
/*
.ng-enter, .ng-leave{
	@include vendor(transition, all $dur ease);
}
*/

// loader
.loading {

  &.ng-enter {
    top: -50px;
  }
  &.ng-enter-active {
    top: 0;
  }
  &.ng-leave {
    top: 0;
  }
  &.ng-leave-active {
    top: -50px;
  }
}

// dimmer
.dimmer {
  &.ng-enter {
    opacity: 0;
  }
  &.ng-enter-active {
    opacity: 1;
  }
  &.ng-leave {
    opacity: 1;
  }
  &.ng-leave-active {
    opacity: 0;
  }
}
