// menu
.menu {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  background: $white;

  @include vendor(box-shadow, inset -40px 0 40px -45px $black);

  .header {
    p {
      padding: 0;
      margin: 0;
    }
    font-size: 12px;
    text-transform: uppercase;
    color: $white;
    background: $red_dark;
    @include vendor(box-shadow, inset 0px -2px 0px 0 $dark-overlay);
    padding: 7px 11px;

    &.branded-medium {
      background: $default-brand-color;
      @include vendor(box-shadow, inset 0px -2px 0px 0 $dark-overlay);
    }
  }

  .item {

    &.home {
      line-height: 48px;
      @media (max-width: $breakpoint-small-max) {
        line-height: 27px;
      }

      .icon {
        line-height: 48px;
        @media (max-width: $breakpoint-small-max) {
          line-height: 27px;
        }
      }

      .current+.close-btn .fa {
        color: $white;
      }
      .close-btn {
        position: absolute;
        right: 0;
        top: 0;
        padding: 12px;
        &:active {
          background: none;
        }

        .fa {
          color: $black;
        }

      }
    }

    border-bottom: 2px solid $grey;

    &.panel a {
      padding: 7px 0 11px 11px;
    }
    a {
      //$small-breakpoint-menu-counter-margin: 40%;

      color: $black;
      padding: 11px;
      display: block;

      &:hover {
        text-decoration: none;
      }

      &:active, &.current {
        background: $black;
        color: white;

        .num-of-activities {
          color: $black;
        }
      }

      .num-of-activities {
        background: $bg_grey;
        padding: 5px 8px;
        float: right;
        border-radius: 25%;
        position: relative;
        top: -4px;
      }
    }
    .fa {
      display: inline-block;
      margin-right: 10px;
      font-size: 22px;
    }
    .icon {
      display: inline-block;
      margin-right: 15px;
      font-size: 22px;
      vertical-align: bottom;

      img {
        width: 22px;
      }
    }
    .icon.panel {
      border-radius: 50%;
      width: 25px;
      height: 25px;
      position: relative;
      img {
        width: auto;
        position: absolute;
        left: 5px;
        top: 50%;
        margin-top: -7.5px;
        height: 15px;
      }
    }

    //colors
    .fa-exclamation {
      color: $green_dark;
    }
    .fa-check {
      color: $yellow_dark;
    }
    .fa-times {
      color: $red_dark;
    }
  }
}