// Override core-layout defaults.
$cl-header-height-xs: 42px;
$cl-header-height-sm: 72px;
$cl-header-height-md: $cl-header-height-sm;
$cl-header-height-lg: $cl-header-height-md;

$cl-screen-sm-min: $breakpoint-small-min;
$cl-screen-md-min: $breakpoint-medium-min;
$cl-screen-lg-min: $breakpoint-large-min;
// The $cl-screen-*-max variables are defined by the -min values.

$cl-modal-header-height-xs: 42px;
$cl-modal-footer-height-xs: 56px;

$cl-modal-header-height-sm: $cl-modal-header-height-xs;
$cl-modal-footer-height-sm: $cl-modal-footer-height-xs;

$cl-modal-header-height-md: $cl-modal-header-height-sm;
$cl-modal-footer-height-md: $cl-modal-footer-height-sm;

$cl-modal-header-height-lg: $cl-modal-header-height-md;
$cl-modal-footer-height-lg: $cl-modal-footer-height-md;

$cl-modal-box-small-width-sm: 550px;
$cl-modal-box-small-height-sm: 400px;

$cl-modal-box-small-width-sm: 550px;
$cl-modal-box-small-height-sm: 400px;
$cl-modal-box-small-width-md: $cl-modal-box-small-width-sm;
$cl-modal-box-small-height-md: $cl-modal-box-small-height-sm;
$cl-modal-box-small-width-lg: $cl-modal-box-small-width-md;
$cl-modal-box-small-height-lg: $cl-modal-box-small-height-md;

$cl-modal-box-medium-width-sm: 600px;
$cl-modal-box-medium-height-sm: $cl-modal-box-medium-width-sm;
$cl-modal-box-medium-width-md: 600px;
$cl-modal-box-medium-height-md: $cl-modal-box-medium-width-md;
$cl-modal-box-medium-width-lg: 600px;
$cl-modal-box-medium-height-lg: $cl-modal-box-medium-width-lg;

$cl-modal-box-large-width-sm: $breakpoint-small-min - 10px;
$cl-modal-box-large-height-sm: $cl-modal-box-large-width-sm;
$cl-modal-box-large-width-md: $breakpoint-medium-min - 10px;
$cl-modal-box-large-height-md: $cl-modal-box-large-width-md;
$cl-modal-box-large-width-lg: $breakpoint-medium-min;
$cl-modal-box-large-height-lg: $cl-modal-box-large-width-lg;


$cl-left-drawer-width-xs: 250px;
$cl-left-drawer-width-sm: 197px;
$cl-left-drawer-width-md: 248px;
$cl-left-drawer-width-lg: 208px;

