#loading-bar .bar {
  background: $grey_dark;
  height: 4px;
  position: absolute;
}

#loading-bar-spinner {
  $spinner-size: 24px;
  position: absolute;
  left: auto;

  @media (max-width: $breakpoint-small-max) {
    top: 10px;
    right: $spinner-size + 12px + $margin;
  }
  @media (min-width: $breakpoint-small-min) {
    top: 22px;
    right: $spinner-size + 20px + $margin;
  }

  .spinner-icon {
    width: $spinner-size;
    height: $spinner-size;

    border: solid 2px transparent;
    border-top-color: $white;
    border-left-color: $white;
    border-radius: $spinner-size;
  }
}