.fix-fixed {

  .activity-popup {
    @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
      position: absolute;
    }
    @media (min-width: $breakpoint-large-min) {
      position: absolute;
    }
    @media (max-width: $breakpoint-small-max) {
      position: absolute;
    }
  }

  .pop {
    @media (max-width: $breakpoint-small-max) {
      position: absolute;
    }
    @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
      position: absolute;
    }
    @media (min-width: $breakpoint-large-min) {
      position: absolute;
    }
  }

  .dimmer {
    @media (min-width: $breakpoint-small-min) {
      position: absolute;
    }
  }

  .pop.modal-attachment {
    @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
      position: absolute;
    }
    @media (min-width: $breakpoint-large-min) {
      position: absolute;
    }
  }

  .growl-container {
    position: absolute;
  }


  .loading {
    position: absolute;
  }

  .logged-in#main-wrapper {
    &.open {
      @media (max-width: $breakpoint-small-max) {
        position: absolute;
      }
    }
  }

  .menu {
    @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
      position: absolute;
    }

    @media (min-width: $breakpoint-large-min) {
      position: absolute;
    }
  }

  .sticky-carousel {
    position: absolute;
  }

  .dropdown-backdrop {
    // I'm very uncertain about this one.
    position: absolute;
  }

  .cfp-hotkeys-container {
    position: absolute;
  }
  .cfp-hotkeys-close {
    position: absolute;
  }
}
