$activity-height-small: 120px;
$activity-height-medium: 160px;

.active.activity-item .meta .status {
	color:$yellow !important;
}

.closed.activity-item .meta .status {
	color:$red !important;
}

.new.activity-item .meta{
    .status {
	    color:$green !important;
    }

	.new-circle{
		width:12px;
		height:12px;
		display:inline-block;
		background:$green;
		@include vendor(border-radius, 50%);
		position: relative;
		top: 1px;
		margin-right: 5px;
	}
}

.top {
    position: relative;
    padding: 0 $padding;

    .meta, .points-date {
        margin: 0;
        font-size: 12px;
        color: $grey;
        text-transform: uppercase;
        position: absolute;

        @media (max-width: $breakpoint-small-max) {
            font-size: 11px;
        }
    }
    .meta {
        top: 0;
        left: 0;
    }
    .points-date {
        top: 0;
        right: 0;
    }
}

.activity-item{
	
	position:relative;	

	&.impact{
		background-image: url(../img/ui/impact-border.png);
		background-position: top right;
		background-size: 25px;
		background-repeat: no-repeat repeat;
		background-color: #fec76e;

		h2, .desc{
			color:#744513 !important;
		}

		.meta-bar p, .meta-bar span{
			color: #c26b20 !important;
		}
	}

	background:$white;
	margin-bottom:$margin;

	.image{

		height:200px;
		width:200px;

		@media (max-width: $breakpoint-small-max){
			height: $activity-height-small;
			width: $activity-height-small;
		}
		
		@media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max){
            height: $activity-height-medium;
			width: $activity-height-medium;
		}

		//background-image:url('http://placehold.it/400x400');
		display:block;	
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		position:relative;
	}
	
	.content{
		padding: 5px 10px 10px 10px;
		width:63%;
		
		@media (min-width: $container-max-width){
			width:68%;
		}

		@media (max-width:1033px) and (min-width:998px){
			width:62%;
		}

		@media (max-width:997px) and (min-width:758px){
			width:69%;
		}

		@media (max-width:790px) and (min-width:708px){
			width:65%;
		}

		@media (max-width:709px) and (min-width:658px) {
			width:63%;
		}

		@media (max-width:659px) and (min-width:601px){
			width:59%;
		}

		@media (max-width: $breakpoint-small-max){
			width:55%;
		}

		.top{
			.points-date {
				@media (max-width: $breakpoint-small-max){
					left:0;
					bottom: -$activity-height-small;
					position:absolute;
					top:auto;
					right:auto;
                    margin-bottom: 5px;
				}
			}
		}

		.name{
			font-size: 24px;
			line-height: 24px;
			margin-bottom:$margin/2;

			@media (max-width:$breakpoint-small-max){
				font-size: 14px;
				line-height:20px;
			}

			@media (min-width: $breakpoint-small-min) and (max-width:$breakpoint-large-max){
				font-size:18px;
				line-height:18px;
			}
		}
		p{
			margin:0;

			@media (min-width: $breakpoint-small-min) and (max-width:$breakpoint-large-max){
				font-size: 12px;
				line-height: 13px;
			}

		}

		.buttons{
			display: block;
			position: absolute;
			bottom: 5px;
			width: 70%;
			text-align: center;
			left: 30%;
		}

	}
	
	//
	// in progress
	//

	.in-progress{
		height: 2px;
		overflow: hidden;
		background: $bg_grey;
		position: absolute;
	
		bottom: 50px;

		@media (max-width: $breakpoint-small-max){
			bottom: 30px;
		}

		width:61%;
		@media (min-width: $container-max-width){
			width:66%;
		}

		@media (max-width:1033px) and (min-width:998px){
			width:60%;
		}

		@media (max-width:997px) and (min-width:758px){
			width:67%;
		}

		@media (max-width:790px) and (min-width:708px){
			width:63%;
		}

		@media (max-width:709px) and (min-width:658px) {
			width:61%;
		}

		@media (max-width:659px) and (min-width:601px){
			width:57%;
		}

		@media (max-width: $breakpoint-small-max){
			width:53%;
		}

		.bar{
			height: 5px;
			display: block;
			position: relative;
			width: 100%;
			.progress{
				height: 5px;
				background: $blue;
			}

		}
	}

}