.main-layout-wrapper {
  padding: 0;

  @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
    padding-right: $padding;
  }

  @media (min-width: $breakpoint-large-min) {
    padding-right: $padding;
  }

  &.home {
    margin-top: 0;
  }
}

#main-wrapper {

  // main
  background: $bg_grey;
  margin-top: 0;

  @media (max-width: $breakpoint-small-max) {
    -webkit-transition: all 300ms ease;
    z-index: z("main-wrapper");
  }
}

#main-wrapper:not(.logged-in) {
  width: 100%;
}

.logged-in#main-wrapper {

  &.open {
    @media (max-width: $breakpoint-small-max) {
      -webkit-transform: translate3d(65%, 0, 0);
      height: 100%;
      position: fixed;
    }
  }

  @media (max-width: $breakpoint-small-max) {}

  @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
    left: 30%;
  }

  @media (min-width: $breakpoint-large-min) {
    left: 20%;
  }

  @media (min-width: $container-max-width) {
    left: 20%;
  }

}

.wrap {
  position: relative;
  padding: 10px;
}
