.is-stimulus {
  .attachment > div {
    text-align: center;
  }
}

.attachment {

  .image {
    @include vendor-arg(cursor, zoom-in);
    width: inherit;
    max-width: 100%;
    max-height: 1 / 3 * 100%;
  }
  .image, .video {
    padding-bottom: $padding;
  }
  .video-zoom {
    position: absolute;
    z-index: z("video-popup");
    cursor: -webkit-zoom-in;
    cursor: -moz-zoom-in;
    right: 0;
    margin: 10px 28px 0 0;
  }
  .fa {
    color: $white;
  }
}

.message-editor {
  .attachment {
    @extend .attachment;
    color: $grey_dark;

    .image, .video {
      max-width: 50%;
    }
  }
}

.view-attachment {
  background: $white;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

