
.welcome-logo {
  height: 400px;
  background-image: url('../img/welcome-logo.jpg');
  background-size: cover;
  background-position: center center;
  background-repeat: repeat-x;

  @media (max-width: $breakpoint-small-max) {
    height: 200px;
  }

  @media (min-width: $breakpoint-small-min) and (max-width: $breakpoint-large-max) {
    height: 300px;
  }
}

.welcome-text {
  background: $white;
  margin-top: 0 !important;
  padding: $padding * 4 0;

  text-align: center;

  p {
    width: 70%;
    margin: 0 auto $padding * 3 auto;
  }

  .buttons {
    margin-top: $margin;

    a {
      margin: 0 $margin;
      width: 20%;

      @media (max-width: $breakpoint-small-max) {
        width: 40%;
      }
    }
  }

  form {

    .button {
      width: 100%;
    }

    .or {
      width: 100%;
      text-transform: uppercase;
      margin: $margin * 2 0;
    }

    width: 42%;
    margin-left: auto;
    margin-right: auto;
    margin-top: $margin;

    input {

      background: $bg_grey;
    }

    .custom.select {
      width: 100%;
      select {
        border: 2px solid $bg_grey;
        padding: 0 $padding;
      }
    }

    .phone-number {
      width: 100%;
      .custom.select {
        @media (max-width: $breakpoint-medium-max) {
          width: 100%;
        }
        width: 45%;
      }
      @media (max-width: $breakpoint-medium-max) {
        input[type=tel] {
          width: 100%;
        }
      }
    }
    @media (max-width: $breakpoint-small-max) {
      width: 90%;
      margin: $margin auto 0 auto;
    }
  }

  &.onboarding, &.profile {
    background: $bg_grey;
    padding-top: 0;

    @media (min-width: $breakpoint-medium-min) {
      width: 60%;
      margin: 0 auto;
    }
  }
}

.presentation {
  width: 90%;
  margin-top: $margin * 2 !important;

  .presentation-box {
    padding: $padding;
    text-align: center;

    h2 {
      font-size: 20px;
      margin-bottom: $margin;
    }
  }
}

.is_onboarding.is_reward {
  .welcome-text {
    padding: 0;
  }
  .buttons a {
    margin: 0 $margin;
  }
  .reward {
    padding-bottom: $menu-bar-height;
  }
}

.cl-footer {
  @include vendor(box-shadow, 0px 0 55px -10px black);
}

.welcome-footer {
  padding-top: $padding-small-vertical;
  padding-bottom: $padding-small-vertical;

  .buttons {
    text-align: center;
    a {
      margin: 0 $margin;
    }
  }
}
