@mixin gradient-vertical($firstColor: #FFF, $secondColor: #DDD) {
  background: $firstColor; /* Fallback solid color for old browsers */
  background: linear-gradient(to bottom, $firstColor 0%, $secondColor 100%); /* W3C */
  background: -moz-linear-gradient(top, $firstColor 0%, $secondColor 100%); /* FF 3.6+ */
  background: -ms-linear-gradient(top, $firstColor 0%, $secondColor 100%); /* IE 10+ */
  background: -o-linear-gradient(top, $firstColor 0%, $secondColor 100%); /* Opera 11.10+ */
  background: -webkit-linear-gradient(top, $firstColor 0%, $secondColor 100%); /* Chrome 10+, Safari 5.1+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, $firstColor), color-stop(100%, $secondColor)); /* Chrome, Safari 4+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr= '#{$firstColor}', endColorstr= '#{$secondColor}', GradientType=0); /* IE 6-9 */
}

.ribbon-box {
  position: relative;
}
.ribbon-wrapper {
  width: 106px;
  height: 93px;
  top: 0;
  right: 0;
  overflow: hidden;
  position: absolute;
  z-index: 9999;
}

.ribbon {
  $ribbonFontSize: 12px;
  $ribbonRotation: 45deg;
  font: bold $ribbonFontSize Sans-Serif;
  color: #333;
  text-align: center;
  text-shadow: rgba(255, 255, 255, 0.5) 0px 1px 0px;
  position: relative;
  padding: 7px 0;
  left: 19px;
  top: 12px;
  width: 120px;
  @include rotate($ribbonRotation);
  @include box-shadow(0px 0px 3px rgba(0, 0, 0, 0.3));

  @mixin ribbon-color($ribbon_base_color) {
    $ribbon_darker_color: darken($ribbon_base_color, 30);
    $ribbon_lighter_color: lighten($ribbon_base_color, 20);

    color: $ribbon_darker_color;
    @include gradient-vertical($ribbon_lighter_color, $ribbon_base_color);
    &:before, &:after {
      border-top: 3px solid $ribbon_darker_color;
    }
  }

  &.valid {
    @include ribbon-color(#8EBF45);
  }
  &.inactive {
    @include ribbon-color(#E6E6E6);
  }
  &.visible {
    @include ribbon-color(#ff780b);
  }
  &:before, &:after {
    content: "";
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    position: absolute;
    bottom: -3px;
  }
  &:before {
    left: 0;
  }
  &:after {
    right: 0;
  }
}