// layout
$popup-z-index: 200;
$container-max-width: 1220px;

@function decr($i) {
  @return $i - 1;
}

// breakpoints
$breakpoint-small-min: 660px;
$breakpoint-small-max: decr($breakpoint-small-min);
$breakpoint-medium-min: 768px;
$breakpoint-medium-max: decr($breakpoint-medium-min);
$breakpoint-large-min: 1000px;
$breakpoint-large-max: decr($breakpoint-large-min);

//vars
$padding: 10px;
$padding-small-vertical: $padding / 2;

$margin: 10px;

$menu-bar-height: 45px;
// Top of the screen menu bar on small devices.
$menu-item-height: 50px;
// Item in left-hand menu.

$brand-bar-y-padding: 15px;

//$buttons-bar-height: 55px;
$view-offset: 45px;
$view-offset-top: $view-offset;
$view-offset-bottom: $view-offset;

// z-index management (http://www.sitepoint.com/better-solution-managing-z-index-sass/)
$z-layers: (
    // Names are best-effort, values have been gathered from around the other
    // scss files and seem to be arbitrary. Feel free to improve.
    "popup": $popup-z-index,
    "video-popup": 100,
    "small-popup": 1000,
    "main-overlay": 1500,
    "button-as-overlay": 200,
    "button": 100,
    "icon": 1,
    "icon-overlay": 2,
    "icon-select": 9,
    "smiley": 1,
    "happy-guy-bubble": 2,
    "growl-container": 1042,
    "loader": 100,
    "main-wrapper": 11,
    "menu-small": 10,
    "notifications-popup": 100,
    "sticky-carousel": 200,
    "licenses-button": 1,
    "img-cropper": 600,
    "dimmer": $popup-z-index - 1,
    "bootstrap-dropdown": 2003,
);

@function z($layer) {
  @if not map-has-key($z-layers, $layer) {
    @warn "No layer found for `#{$layer}` in $z-layers map. Property omitted.";
  }

  @return map-get($z-layers, $layer);
}

// core-layout overrides
$cl-header-height: 45px;


