.header, .activity-popup h1 {
  background: $blue;

  @include vendor(box-shadow, inset 0px -2px 0px 0 rgba(0, 0, 0, .2));

  .logo {
    color: $white;
    width: 100%;
    text-align: left;
    margin: 0;
    padding: $brand-bar-y-padding;

    @media (max-width: $breakpoint-small-max) {
      padding: 0;
    }
  }
}

.cl-header .header {
  @media (max-width: $breakpoint-small-max) {
    height: 45px;
  }
}