.profile-box {

  margin-top: $margin;
  //  background: $blue;
  background: $default-brand-color;
  position: relative;

  .p-info {
    position: absolute;
    top: 5px;
    left: $padding;
    text-transform: uppercase;
    font-size: 12px;
    color: $white;
    text-decoration: underline;
  }

  .name {
    font-size: 17px;
    color: $white;
    margin-top: $margin * 3;
    padding-left: $padding;
    line-height: 16px;
  }

  .image {
    padding: $padding;
    width: 90px;
    .circle {
      @include vendor(border-radius, 50%);
    }
  }

  .meta {
    margin: 0;

    .item {
      padding: $padding;

      * {
        color: $white;
      }

      p {
        margin: 0;
      }

      .big {
        color: white;
        font-size: 18px;

        .i {
          color: $white;
          margin: 0 3px;
          font-size: 18px;
          position: relative;
          top: 1px;
        }
      }
      .small {
        text-transform: uppercase;
        font-size: 10px;
        color: $white;
      }
    }
  }
}
