.phone-number {
  width: 95%;
  margin-left: auto;
  margin-right: auto;

  .custom.select {
    @media (max-width: $breakpoint-small-max) {
      width: 100%;
    }
    width: 45%;
    margin-left: 0;
    margin-right: 2%;
    display: inline-block;
    vertical-align: middle;

    select {
      margin-bottom: auto;
    }
  }
  input[type=tel] {
    @media (max-width: $breakpoint-small-max) {
      width: 100%;
    }
    width: 52%;
    display: inline-block;
  }
}

.phone-label {
  @media (max-width: $breakpoint-small-max) {
    &.aggregate {
      display: none;
    }
    &.detailed {

    }
  }
  @media (min-width: $breakpoint-small-min) {
    &.aggregate {

    }
    &.detailed {
      display: none;
    }
  }
}
