$modal-font-size-h1: 20px;

#cl-modal > .cl-header {
  .close-btn {
    position: absolute;
    width: 40px;
    height: 40px;
    padding: 9px 8px 5px 8px;
    font-size: 24px;
    color: white;
    z-index: z("button");
    top: 0;
    right: 5px;
  }

  h1 {
    color: white;
    text-align: center;
    font-size: $modal-font-size-h1;
    margin: 0;
    background: $blue;
  }
}

#cl-modal > .cl-contents {
  .welcome-text {
    padding: 20px 0 35px 0;
  }
}