
.shows-diary {
  .cl-modal-box .cl-contents {
    overflow: hidden;
  }

  .scrollable-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow-y: auto;
    background-color: $bg_grey;
  }
}

.iscroll-on .diary {
    height: 100%;
}

.diary {
  background: $bg_grey;

  .question {
    width: 100%;
    height: 100%;
  }

  .d-header {
    float: left;
    font-size: 24px;
    margin-bottom: 10px;
    margin-left: 10px;
    width: 65%;
  }
  .diary-icon {
    display: inline-block;
    float: left;
    width: 90px;
    height: 90px;
    background: url('../img/ui/diary-icon.png') center center no-repeat;
    background-size: 90px;
  }

  .circle-wrap.status-true {
    background: $green;
  }
  .circle-wrap.status-false {
    background: $bg_grey;
    overflow: hidden;
  }
  .circle-wrap.status-incomplete {
    overflow: hidden;
    span {
      height: 20px;
      width: 10px;
      border-radius: 0 20px 20px 0;
      background: $bg_grey;
      display: block;
      position: absolute;
      right: 0;
    }
    span::before {
      content: '';
      height: 20px;
      width: 10px;
      border-radius: 20px 0 0 20px;

      background: $yellow;
      display: block;
      position: absolute;
      left: -10px;
    }
  }

  .upper-small {
    text-transform: uppercase;
    font-size: 14px;
    margin-bottom: 5px;
  }

  .list-item {
    cursor: pointer;
    position: relative;
    background: white;
    padding: 15px 10px;
    margin-bottom: 10px;
    overflow: hidden;
    @include vendor(box-shadow, inset 0 -4px 0 #{'rgba(0,0,0,.2)'});
    p {
      color: $grey_dark;
      padding-right: 50px;
    }
    .circle-wrap {
      display: block;
      width: 20px;
      height: 20px;
      @include vendor(border-radius, 50%);
      top: 50%;
      right: 20px;
      position: absolute;
      @include vendor(transform, translateY(-50%));

      span {
        position: absolute;
      }
    }
  }

  .completed-list-item, .incomplete-list-item {
    cursor: pointer;
  }

  .completed-list-item {
    position: relative;
    background: white;
    margin-bottom: 10px;
    overflow: hidden;
    box-shadow: inset 0 -4px 0 #{'rgba(0,0,0,.2)'};
    .circle-wrap {
      display: block;
      width: 20px;
      height: 20px;
      overflow: hidden;
      border-radius: 50%;
      top: 50%;
      right: 20px;
      position: absolute;
      -webkit-transform: translateY(-50%);

      span {
        position: absolute;
      }
    }
    .date-wrap {
      display: block;
      width: 80px;
      float: left;
      background: $blue;
      box-shadow: inset 0 -4px 0 #{'rgba(0,0,0,.2)'};

      .date, .day {
        color: white;
        text-transform: uppercase;
      }
      .date {
        font-size: 30px;
        display: block;
        text-align: center;
        margin: 20px 0 10px 0;
      }
      .day {
        text-align: center;
        font-size: 12px;
        margin-bottom: 10px;
      }
    }
    .info {
      display: inline-block;
      padding: 10px;
      float: left;
      width: 70%;
      height: 76px;

      .head {
        text-transform: uppercase;
        font-size: 12px;
      }
      .date {
        font-size: 18px;
      }
    }
  }
}