.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  background: $grey_dark;
  color: $white;
  padding: $padding;
  margin: 0;
  font-size: 14px;
  text-transform: uppercase;
  z-index: z("loader");
  @include vendor(box-shadow, 0 -11px 29px);

  span {
    position: relative;
    top: 2px;
  }
  img {
    width: 25px;
    height: 25px;
    display: inline-block;
    margin-left: 10px;
  }
}