.notifications-header {
  position: relative;
  font-size: 18px;
  background: $grey_dark;
  padding: $padding;
  color: white;
  @include vendor(box-shadow, inset 0 -2px 0 #262a2e);
  margin: $margin 0 0 0;

  .clear {
    position: absolute;
    right: 10px;
    text-transform: uppercase;
    font-size: 14px;
    padding: 0 7px;
    background: $black;
    @include vendor(border-radius, 5px);
    color: white;

    &:hover {
      text-decoration: none;
    }
    &:active {
      background: white;
      color: $black;
    }
  }
}

#cl-right-drawer {
  .notifications-header {
    line-height: 48px;
    margin: 0;
    padding: 14px;
    font-size: 14px;

    @media (max-width: $breakpoint-small-max) {
      line-height: 27px;
      padding: 10px;
    }

    .icon {
      display: inline-block;
      color: $white;
      font-size: 22px;
      vertical-align: bottom;

      line-height: 48px;
      @media (max-width: $breakpoint-small-max) {
        line-height: 27px;
      }
    }
  }
}

.notifications-header .icon {
  display: none;
}

.notifications.icon-button {
  &.ng-click-active {
    background: rgba(0, 0, 0, .1)
  }

  padding: 0;
  margin: 0;
  display: inline-block;
  height: 50px;
  position: absolute;

  @media (max-width: $breakpoint-small-max) {
    top: 8px;
    right: 12px;
  }
  @media (min-width: $breakpoint-medium-min) {
    top: 20px;
    right: 20px;
  }

  background: none;
  box-shadow: none;

  .icon-NewActivities {
    padding: 2px;
    @include vendor(border-radius, 50%);
    border: 3px solid white;
    font-size: 17px;
  }
}

.notifications-drawer {
  height: 100%;
  background: $black;
}

.scrollable-notifications {
  position: relative;
  width: 100%;
  overflow-y: auto;
  height: 164px;
  margin-bottom: $margin;
  background: $black;
}

.notifications {
  margin: 0;
  padding: 0;
  background: $black;

  ul {
    margin: 0;
    padding: 0;
  }
  li {
    list-style: none;
  }

  .item {
    position: relative;
    border-bottom: 1px solid $grey;

    p {
      color: #d4dfe5;
      display: block;
      padding: $padding;
      text-decoration: none;
      margin: 0;
    }
    span {
      color: $white;
      display: inline-block;
      font-size: 22px;
      position: absolute;
      top: 50%;
      margin-top: -12px;
      left: 10px;
    }
    a {
      color: #d4dfe5;
      display: block;
      padding: $padding;
      text-decoration: none;

      &.ng-click-active {

        background: #d4dfe5;
        color: #333333;
      }
    }
  }
}